import React from "react";
import { Grid, Button } from "@material-ui/core";

import { makeStyles } from "@material-ui/core/styles";

import clsx from "clsx";
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import useMediaQuery from '@material-ui/core/useMediaQuery';

import { useTheme } from '@material-ui/core/styles';

const useStyles = makeStyles(({ palette, ...theme }) => ({

  h1: {
    fontFamily: "LEMON MILK",
    fontStyle: "normal",
    fontWeight: "500",
    fontSize: "26px",
    color: "#182731"
  },

  number: {
    fontFamily: "LEMON MILK",
    fontStyle: "bold",
    fontWeight: "700",
    fontSize: "18px",
    color: "#182731",
    alignItems: "center",
    justifyContent: "center"
  },

  title: {
    fontFamily: "LEMON MILK",
    fontStyle: "normal",
    fontWeight: "400",
    fontSize: "14px",
    color: "#182731",
    //textAlign: "left",
    alignItems: "center",
    justifyContent: "center"
  },

  title2: {
    fontFamily: "LEMON MILK Light",
    fontStyle: "normal",
    fontWeight: "400",
    fontSize: "12px",
    color: "#182731",
    lineHeight: "22px",
    alignItems: "center",
    justifyContent: "center"

  },

  description: {
    fontFamily: "LEMON MILK Light",
    fontStyle: "normal",
    fontWeight: "500",
    height: "50%",
    fontSize: "10px",
    color: "#182731"
  },

  imgShadow: {
    boxShadow: "0px 10px 20px rgba(0, 0, 0, 0.25)",
    background: "#FFFFFF",
    padding: "2rem",
    width: "326px",
    height: "150px",
    borderRadius: "0px 50px"
  }
}));

const SixSection = () => {


  const programList1 = [ 
    {
    id: "1",
     title: "CALIFORNIEN",
     title2: "Mthode des 9 Sens Manoeuvres de base dos - Aromathrapie",
     description:
      "Dans le module 1 vous dcouvrirez lutilit de la mthode des 9 SENS  Nous commencerons par des manuvre de base du dos avec dos (mouvement lent et ample) ainsi que lutilisation de laromathrapie  et des autres sens.",
    },

    {
      id: "2",
      title: "SHIATSU",
      title2: "Dos approfondissement pression progressive et tirements doux",
      description: 
      "Dans le module 2 vous pourrez  approfondir vos connaissances avec des pression de Shiatsu progressive et tirement doux du dos (travail avec les pouces et le plat de la main).",
    },

    {
      id: "3",
      title: "SUEDOIS",
      title2: "Grand dos rvision - Profondeur",
      description:
       "Dans le module 3 vous verrez lutilit du massage sudois avec du ptrissage et nous en profitons pour revoir lensemble du massage du dos mais avec des mouvements plus profond et plus saccads.",
    }
  ]


  const programList2 = [
    {
      id: "4",
      title: "INDIEN",
      title2: "Pierres chaudes - Jambes - fessiers - pieds sur l'paule",
      description:
       "Dans le module 4 nous aborderons les techniques Indiennes avec le massage avec les Pierres chaudes  sur le dos mais aussi  le massage des Jambes - fessiers - et ce nous appelons le (pieds sur lpaule)",
    },

    {
      id: "5",
      title: "TIBETAIN",
      title2: "Bol tibtain, bol en cristal",
      description:
       "Dans le module 5, vous ressentirez les bienfaits des ondes sonores avec les Bols tibtains et les bols en crystal",
    },

    {
      id: "6",
      title: "9 SENS (rappel)",
      title2: "Fluidit des enchanements - pieds jambes",
      description:
       "Dans ce module 6 nous rviserons les bases pour rappel des techniques nous accentuerons lapprentissage sur la bonne fluidit des enchanements  et nous verrons les bienfait du massage des pieds et des jambes.",
    },
  ]

  const programList3= [
    
    {
      id: "7",
      title: "AYURVEDIQUE",
      title2: "Ventre - plexus solaire",
      description:
       "Dans le module 7 (correspondant au 7 chakras) vous apprendrez  relier les chakras avec le massage du ventre  et du plexus solaire avec des mouvements ayurvdique.",
    },

    {
      id: "8",
      title: "SHIATSU",
      title2: "Nuque - clavicule",
      description:
       "Dans le module 8, nous reviendrons sur les mouvement de Shiatsu mais ceux ci seront lent et profond pour masser dlicatement la nuque , les omoplates , et les clavicules.",
    },

    {
      id: "9",
      title: "CALIFORNIEN",
      title2: "Tte - visage",
      description:
       "Dans le module 9 nous terminerons par des mouvement de californien fluide et lent en massant dlicatement la tte et le visage. Nous verrons galement des mouvements Indiens sur ces mmes partis du corps.",
    },
  ]

  const classes = useStyles();

  const [open, setOpen] = React.useState(false);

  const theme = useTheme();

  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
  
    <div className="section-programme-formation">
    <div className="container">

    <h1 className={clsx(classes.h1, "text-center mt-5 mb-12")}> Programme </h1>

    <Grid sm={12} md={12} xs={12} lg={12} container justify="center" alignItems="center">
   
    {programList1.map(item => (

    
    <Grid sm={3} md={3} xs={12}>

    <div className={clsx(classes.imgShadow, "mt-5")} >

    <h1 className={classes.number}>  {item.id }</h1>

   <h1 className={classes.title}>  {item.title} </h1>

   <h4 className={classes.title2}>
   
   {item.title2}
   
   </h4> 

</div>
     
   </Grid>
    ))}

    </Grid>

    <Grid sm={12} md={12} xs={12} lg={12} container justify="center" alignItems="center">
   
    {programList2.map(item => (

    <Grid sm={3} md={3} xs={12}>

    <div className={clsx(classes.imgShadow, "mt-5")} onClick={handleClickOpen} >

    <h1 className={classes.number}> {item.id} </h1>

   <h1 className={classes.title}> {item.title} </h1>

   <h4 className={classes.title2}>
   
   {item.title2}
   
   </h4> 

   <Dialog
        fullScreen={fullScreen}
        open={open}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="responsive-dialog-title">{"Use Google's location service?"}

        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            Let Google help apps determine location. This means sending anonymous location data to
            Google, even when no apps are running.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleClose} color="primary">
            Cancel
          </Button>
        </DialogActions>
      </Dialog>

   </div>
     
   </Grid>
    ))}

    </Grid>


    <Grid sm={12} md={12} xs={12} lg={12} container justify="center" alignItems="center">
   
    {programList3.map(item => (

    
    <Grid sm={3} md={3} xs={12}>

    <div className={clsx(classes.imgShadow, "mt-5")} >

    <h1 className={classes.number}> {item.id} </h1>

   <h1 className={classes.title}> {item.title} </h1>

   <h4 className={classes.title2}>
   
   {item.title2}
   
   </h4> 

</div>
     
   </Grid>
    ))}

    </Grid>


  </div>
  </div>
 

  );
};

export default SixSection;
